<template>
  <div class="role-create">
    <c-form
      :submit="create"
      :columns="columns"
      :items="items"
      :actions="actions">
    </c-form>
  </div>
</template>

<script>
import CForm from 'components/c-form'
import { roles } from 'vx/getters'
import { createRole } from 'vx/actions'
import { ROLE_LEVEL_OPTIONS } from 'vx/constants'
export default {
  data () {
    return {
      columns: {
        name: {
          label: '',
          type: 'text',
          validate: {
            required: {
              rule: true,
              message: ''
            },
            maxlength: {
              rule: 20,
              message: ' 20 '
            }
          }
        },
        desc: {
          label: '',
          type: 'text',
          validate: {
            required: {
              rule: true,
              message: ''
            },
            maxlength: {
              rule: 100,
              message: ' 100 '
            }
          }
        },
        level: {
          label: '',
          type: 'dropdown',
          attrs: {
            options: ROLE_LEVEL_OPTIONS
          },
          validate: {
            required: {
              rule: true,
              message: ''
            }
          }
        }
      },
      items: {
        name: '',
        desc: '',
        level: 0
      },
      actions: [null, {
        submit: {
          type: 'submit',
          class: 'primary',
          // string or function
          label: this.progress ? '...' : '',
          disabled: !!this.progress
        }
      }]
    }
  },

  watch: {
    roles: {
      handler (val) {
        this.$nextTick(() => {
          if (val.items.length) {
            this.$route.router.go('/roles')
          }
        })
      },
      deep: true
    }
  },

  methods: {
    create ($validation, $payload) {
      if (!$validation.valid) {
        return
      }
      this.createRole($payload)
    }
  },

  vuex: {
    getters: {
      roles
    },
    actions: {
      createRole
    }
  },

  components: {
    CForm
  }
}
</script>



